import React from "react";
import Table from "react-bootstrap/Table";

const StaticTable = ({
  data,
  handleCheckboxChange,
  checkedItems,
  handleSelectChange,
  selectedAttributes,
  mappedAttributes, // Passed mapped attributes
}) => {
  console.log("StaticTable Data prop:", data);
  console.log("Mapped Attributes prop:", mappedAttributes);

  return (
    <Table bordered className="table-light">
      <thead>
        <tr>
          <th></th>
          <th>Column Name</th>
          <th>Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {data.map((row, index) => (
          <tr key={index}>
            <td>
              <input
                type="checkbox"
                checked={checkedItems[index] || false}
                onChange={() => handleCheckboxChange(index)}
              />
            </td>
            <td>{row.columnName}</td>
            <td>
              <select
                className="form-select"
                value={selectedAttributes[index] || row.attributeName}
                onChange={(e) => handleSelectChange(index, e.target.value)}
              >
                <option value={row.attributeName}>{row.attributeName}</option>
                {mappedAttributes.map((attr, attrIndex) => (
                  <option key={attrIndex} value={attr.backendName}>
                    {attr.uiLabel} {/* Display UI Label */}
                  </option>
                ))}
              </select>
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};

export default StaticTable;
